*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
    /* background: linear-gradient(90deg, #efd5ff 0%, #515ada 100%); */
    color: white;
    background: rgb(10,10,10);
    background: radial-gradient(rgba(20,31,32,0.9923319669664741) 14%, rgba(31,31,51,1) 69%);
  }
  
  .navbar{
    background-color: rgba(31,31,51,1);
  background: radial-gradient(  rgba(31,31,51,1) 89%);
   position: sticky;
   top: 0;
   z-index: 1;
color:white;
box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;

}

.btn{
    
    width: 65px;
    height: 30px;
    background: rgb(243, 14, 79);
    transition: width 2s, height 2s;
    border-radius: 5px; 
    
}
.btn:hover{
    width: 85px;
    background: #f565a3;
        transition: width 1s;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: 1s;
      
}

.frame {
    position: relative;
    width: 400px;
    height: 400px;
    border-radius: 2px;
    overflow: hidden;
    color: #333;
    font-family: "Open Sans", Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  
  
  figure {
    height: 100%;
}
.image-1 {
      width: 67%;
      height: 100%;
      border-radius: 10px;
      position: relative;
      background-image: url('https://i.ibb.co/4Td7wQ7/propic.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 20%;
    transition: all 1s ease-in-out;
}
.image-2 {
    display: inline-block;
    width: 50px;
    border-radius: 10px;
    height: 100%;
    position: relative;
    top: -399px;
    left: 20%;
    opacity: 0;
    background-image: url('https://i.ibb.co/Wky1Hjt/propic2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 1s ease-in-out;
  }
   #home:hover .image-2 {
    opacity: 1;
    transform: translate(0) !important;
  }
  
   .image-2:nth-child(2) {
    background-position: 0px 0px;
    transform: translateY(60px);
  }
   .image-2:nth-child(3) {
    background-position: -50px 0;
    transform: translateY(-60px);
  }
   .image-2:nth-child(4) {
    background-position: -100px 0;
    transform: translateY(60px);
  }
   .image-2:nth-child(5) {
    background-position: -150px 0;
    transform: translateY(-60px);
  }
   .image-2:nth-child(6) {
    background-position: -200px 0;
    transform: translateY(60px);
  }
   .image-2:nth-child(7) {
    background-position: -250px 0;
    transform: translateY(-60px);
  }
   .image-2:nth-child(8) {
    background-position: -300px 0;
    transform: translateY(60px);
  }
  .image-2:nth-child(9) {
    background-position: -350px 0;
    transform: translateY(-60px);
  }
  